"use client"

import { useState } from "react"

const movies = [
  {
    id: 1,
    title: "Avengers",
    time: "6:30 PM",
  },
  {
    id: 2,
    title: "Fast & Furious",
    time: "8:00 PM",
  },
]

export default function CinemaSystem() {
  const [selectedMovie, setSelectedMovie] = useState<number | null>(null)
  const [selectedSeat, setSelectedSeat] = useState<number | null>(null)
  const [booked, setBooked] = useState(false)

  return (
    <main className="min-h-screen bg-black text-white p-10">
      <h1 className="text-4xl font-bold mb-8">
        ðŸŽ¬ ABSOLUTE Cinema Management System
      </h1>

      {/* MOVIES */}
      {!selectedMovie && (
        <div>
          <h2 className="text-2xl mb-4">Select Movie</h2>
          <div className="grid grid-cols-2 gap-6">
            {movies.map((movie) => (
              <button
                key={movie.id}
                onClick={() => setSelectedMovie(movie.id)}
                className="p-6 bg-zinc-800 rounded-xl hover:bg-zinc-700"
              >
                <h3 className="text-xl font-bold">{movie.title}</h3>
                <p className="text-gray-400">{movie.time}</p>
              </button>
            ))}
          </div>
        </div>
      )}

      {/* SEATS */}
      {selectedMovie && !booked && (
        <div>
          <h2 className="text-2xl mb-4 mt-8">Select Seat</h2>

          <div className="grid grid-cols-6 gap-3 max-w-md">
            {Array.from({ length: 24 }).map((_, i) => (
              <button
                key={i}
                onClick={() => setSelectedSeat(i)}
                className={`p-3 rounded-lg text-sm
                  ${
                    selectedSeat === i
                      ? "bg-green-500"
                      : "bg-zinc-700 hover:bg-zinc-600"
                  }`}
              >
                {i + 1}
              </button>
            ))}
          </div>

          <button
            disabled={selectedSeat === null}
            onClick={() => setBooked(true)}
            className="mt-6 px-6 py-3 bg-red-600 rounded-xl disabled:opacity-50"
          >
            Confirm Booking
          </button>
        </div>
      )}

      {/* CONFIRMATION */}
      {booked && (
        <div className="mt-10 bg-zinc-900 p-8 rounded-xl max-w-md">
          <h2 className="text-2xl font-bold mb-3">âœ… Booking Successful</h2>
          <p>Movie ID: {selectedMovie}</p>
          <p>Seat: {selectedSeat! + 1}</p>

          <p className="mt-4 text-green-400">
            ðŸŽŸ QR Ticket Generated (demo)
          </p>

          <button
            onClick={() => {
              setSelectedMovie(null)
              setSelectedSeat(null)
              setBooked(false)
            }}
            className="mt-6 px-4 py-2 bg-white text-black rounded-lg"
          >
            Book Again
          </button>
        </div>
      )}
    </main>
  )
}
